export default function (){
    return [];
}
export function itemsHasErrored(state = false, action) {
    switch (action.type) {
        case 'ITEMS_HAS_ERRORED':
            return action.hasErrored;
        default:
            return state;
    }
}
export function itemsIsLoading(state = false, action) {
    switch (action.type) {
        case 'ITEMS_IS_LOADING':
            return action.isLoading;
        default:
            return state;
    }
}

function deleteImage (state = [], action) {
    switch (action.type) {
        case 'DELETE_IMAGE':
            return [
                ...state.slice(0, action.i),
                ...state.slice(action.i + 1)
            ]
        default:
            return state;
    }
}

function increment_like(state = [], action){
    const i = action.index;
    return [
        ...state.slice(0, i),
        {...state[i], nb_like : state[i].nb_like + 1},
        ...state.slice(i + 1),
    ];
}

function increment_like_suggestion(state = [], action){
    const i = action.index;
    return [
        ...state.slice(0, i),
        {...state[i], nb_like : state[i].nb_like + 1},
        ...state.slice(i + 1),
    ];
}

export function items(state = [], action) {
    //console.log("action.type = " + action.type);
    switch (action.type) {
        case 'ITEMS_FETCH_DATA_SUCCESS':
            return action.payload;
        
        case 'DELETE_IMAGE':
        console.log('delete image reducer = ' + state.Pictures);
        
            return {
                ...state, 
                Pictures: deleteImage(state.Pictures, action)
            };
        case 'INCREMENT_LIKES':
            console.log('USER REDUCER    =>      ' + state);
            return {
                ...state,
                Pictures: increment_like(state.Pictures, action)
            };

         case 'INCREMENT_LIKES_SUGGESTION':
            console.log('USER REDUCER    =>      ' + state);
            return {
                ...state,
                suggestions: increment_like_suggestion(state.suggestions, action)
            };
        case 'SUBSCRIBE':
            return action.payload;
        case 'NEWS_FEED':
            return action.payload;
        case 'SUBSCRIPTION_RANDOM_IMAGE':
            console.log("test2!!!!");
            return action.payload;
        case 'GET_TAGS':
            console.log("test2");
            return action.payload;
        case 'GET_LIKED_PICTURES':
        console.log("payload = " + action.payload.ImageLiked);
         //return action.payload.ImageLiked;
            return  {
                ...state,
                LikedPictures: action.payload.ImageLiked
            }
        case 'SUGGESTION':
            console.log("payload = " + action.payload);
            return  {
                ...state,
                suggestions: action.payload.Suggestion
            }
        case 'PICTURE_UPDATE':
        console.log("picture Update !!");
         return {
                ...state,
                MessageUpdate: action.payload.Message
            }
        case 'ADD_FOLLOW':
            return action.payload;

        case 'USER_FOLLOWED':
            return {
                ...state,
                UserFollowed: action.payload.Followed
            }
        default:
            return state;
    }    
}
