import React from 'react';
import Photo from './Photo';
import PhotoProfil from './PhotoProfil';
import Select from 'react-select';

var options = [
  { value: 'Football', label: 'Football' },
  { value: 'Rugby', label: 'Rugby' },
  { value: 'Basket', label: 'Basket' },
  { value: 'Danse classique', label: 'Danse classique' },
  { value: 'Hip-Hop', label: 'Hip-Hop' },
  { value: 'Vélo', label: 'Velo' },

];

const Profil = React.createClass({

getInitialState: function() {
      return {
          tag1: '',
          tag2: '',
          tag3: ''
        };
    },

handleChangeTag1: function(e) {
      this.setState({tag1: e.value});
      this.refs.tag1_ref.value = e.value;
    },
     handleChangeTag2: function(e) {
      this.setState({tag2: e.value});
      this.refs.tag2_ref.value = e.value;
    },
     handleChangeTag3: function(e) {
      this.setState({tag3: e.value});
      this.refs.tag3_ref.value = e.value;
    },

 /*handleUpload(e){
        e.preventDefault();
        //console.log('Submit form !!!');
        const fileupload = this.refs.fileupload;
        const caption = this.refs.caption.value;
        console.log("caption = " + caption);
        console.log("filepuplad = " + fileupload);
        console.log("filepuplad[0].name = " + fileupload.files[0].name);
        var formData = new FormData();
        formData.append('photo', 'tata');
        this.props.uploadPicture(
            'http://localhost:3000/testForm',
            formData
        )
        //this.refs.loginForm.reset();
    },*/
    componentWillMount(){
        let idUser = JSON.parse(localStorage["session"])[0].id;
        this.props.login("http://localhost:3000/getPicturesProfil?idUser=" + idUser);
    },
    render(){

        if (this.props.isLoading) {
            return <p>Loading…</p>;
        }

        if (this.props.items.Message === 'Success' && this.props.items.Pictures !== undefined) {
            return (
                <section>

                <div>
                <h1>Profil</h1>
                <div className="single-photo">
                    <form action="http://localhost:3000/fileupload" method="post" encType="multipart/form-data">
                     <input type="text" name="idUser" value={JSON.parse(localStorage["session"])[0].id}/>
                        <input type="file" name="filetoupload"/>
                          caption :<input type="text" name="caption"/>
                        <p>
                         <input type="text" name="tag1" ref="tag1_ref"/>
                         <input type="text" name="tag2" ref="tag2_ref"/>
                         <input type="text" name="tag3" ref="tag3_ref"/>
                      <Select name="form-field-name" ref="tag1" value={this.state.tag1} options={options} onChange={this.handleChangeTag1} />
                        <Select name="form-field-name" ref="tag2" value={this.state.tag2} options={options} onChange={this.handleChangeTag2} />
                        <Select name="form-field-name" ref="tag3" value={this.state.tag3} options={options} onChange={this.handleChangeTag3} />
                        </p>
                        <input type="submit"/>
                    </form>
                </div>
            </div>

            <div className="photo-grid">
                    {this.props.items.Pictures.map((post, i) =>
                        <PhotoProfil {...this.props} key={i} i={i} post={post}/>)}
                </div>

                </section>
            )
        }

        return (
            <div>
                <h1>Profil</h1>
                <div className="single-photo">
                    <form action="http://localhost:3000/fileupload" method="post" encType="multipart/form-data">
                        <input type="file" name="filetoupload"/>
                        <input type="text" name="caption"/>
                        <input type="submit"/>
                    </form>
                </div>
            </div>
        )

    }
});

export default Profil;


